import Vue from 'vue'
import Router from 'vue-router'

import Hello from '@/components/Hello'
import Home from '@/components/home'


// import Cjly from '@/components/cjly'
// import Hdy from '@/components/hdy';
//import Gry from '@/components/gry'
//import Zby from '@/components/zby'
//import Qz from '@/components/qz';
//import Zyx from '@/components/zyx';

import zz from '@/util/zzmm';

let Cjly = (resolve) => {
  return require.ensure([], ()=>{
    resolve(require('@/components/cjly'))
  })
}

let Hdy = (resolve) => {
  return require.ensure([], ()=> {
    resolve(require('@/components/hdy'))
  })
}

let Gry = (resolve) => {
  return require.ensure([], () => {
    resolve(require('@/components/gry'))
  })
}

let Zby = (resolve) => {
  return require.ensure([], () => {
    resolve(require('@/components/zby'))
  })
}

let Shangpin = (resolve) => {
  return require.ensure([], () => {
    resolve(require('@/components/shangpin'))
  })
}
let Qz = (resolve) => {
  return require.ensure([], () => {
    resolve(require('@/components/qz'))
  })
}

let Zyx = (resolve) => {
  return require.ensure([], () => {
    resolve(require('@/components/zyx'))
  })
}

Vue.use(Router)
Vue.use(zz)

let router = new Router({
  mode: 'history',
  linkActiveClass: 'is-active',
  scrollBehavior (to, from, savedPosition) { // 点击浏览器的前进后退或切换导航时触发
     //console.log(to) // 要进入的路有对象  去向哪里
     //console.log(from) // 要离开的路有对象  从哪里来
    //console.log(savedPosition) // 记录滚动条的坐标 点击前进后退市就会记录
    // if (savedPosition) {
    //    return savedPosition;
    //  } else {
    //   return {x: 0, y: 0}
    // }
    if (to.hash) { // 利用hash值来定位到某个元素
      return {
        selector: to.hash
      }
    }
  },
  // scrollBehavior (to, from, savedPosition) { // 点击浏览器的前进后退或切换导航时触发
  //   // console.log(to) // 要进入的路有对象  去向哪里
  //   // console.log(from) // 要离开的路有对象  从哪里来
  //   console.log(savedPosition) // 记录滚动条的坐标 点击前进后退市就会记录
  //   if (savedPosition) {
  //     return savedPosition;
  //   } else {
  //     return {x: 0, y: 0}
  //   }
  //   // if (to.hash) { // 利用hash值来定位到某个元素
  //   //   return {
  //   //     selector: to.hash
  //   //   }
  //   // }
  // },
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/cjly',
      name: 'Cjly',
      component: Cjly
    },
    {
      path: '/hdy',
      name: 'Hdy',
      component: Hdy
    },
    {
      path: '/zby',
      name: 'Zby',
      component: Zby
    },
    {
      path: '/gry',
      name: 'Gry',
      component: Gry
    },
    {
      path: '/qz',
      name: 'Qz',
      component: Qz
    },
    {
      path: '/zyx',
      name: 'Zyx',
      component: Zyx
    },

    {
      path: '/shangpin',
      name: 'Shangpin',
      component: Shangpin
    }

  ]
})

export default router



