aaa(){
setTimeout(() => {
if(this.showMenus){
this.styles.left = '0%';
}else{
this.styles.left = '100%';
}
this.showMenus = !this.showMenus;

},100)

},
setFocus () {
this.$refs.search.setFocus()
},
resultClick (item) {
window.open('','_self');

},
getResult (val) {
this.results = val ? getResult(this.value) : []
},
