})
},
onFocus () {
console.log('on focus')
},
onCancel () {
console.log('on cancel')
},

refresh () {},
infinite () {}
}
}

function getResult (val) {
let rs = []
for (let i = 0; i < 5; i++) {
rs.push({
title: `${val} `,
other: i
})
}
return rs
}



















